import { Component, h, Prop, Element } from '@stencil/core';

@Component({
  tag: 'ks-order-toggle',
})
export class OrderChoice {
    @Element() root: HTMLElement;

    @Prop() name: string;
    @Prop() value: string;
    @Prop() text: string;

    @Prop({mutable: true, reflect: true}) active: boolean;

    CheckHandler() {
        const inputs = document.querySelectorAll(`input[name=${this.name}]`) as NodeListOf<HTMLInputElement>;
        const thisInput = this.root.querySelector("input");

        inputs.forEach((input)=>{

            if(input == thisInput)
                this.active = thisInput.checked;
            else {
                const parent = input.parentElement.parentElement as HTMLKsRadioTileElement;
                parent.active = input.checked
            }
        })
    }

    componentDidLoad() {
        this.root.style.display = "inline-block";
    }

    render() {
        const style = {
            fontSize: "18px",
            padding: "8px",
            backgroundColor: this.active ? "#252525" : "#F2F2F2",
            color: this.active ? "white" : "black",
            transition: "background-color ease 0.15s, color ease 0.15s"
        }

        return(
            <label>
                <input hidden type="radio" name={this.name} value={this.value} checked={this.active} onChange={()=>this.CheckHandler()}/>
                <div class="ks-text-decorated uk-text-bold uk-width-1-1 uk-text-center" style={style}>{this.text}</div>
            </label>
        );
    }
}